#about {
  padding-top: 12rem;
  padding-bottom: 15rem;
  margin: 0 auto;
}

#about .row {
  max-width: 850px;
  margin: 0 auto;
}

.about-intro h2 {
  color: #0092cc;
  text-align: center;
}
.about-education {
  margin: 0 20px 80px 20px;
}

.about-education p {
  font-family: "lora-regular", serif;
  font-size: 2rem;
  line-height: 1.8;
  text-align: center;
  color: #888888;
  margin: 1.5rem 0 1.5rem 0;
}

.about-content {
  display: flex;
}
.col-five {
  flex: 50%;
}

.about-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.about-content .about-list li {
  padding: 0 0 1.5rem 0;
  margin-bottom: 0.6rem;
}

.about-content .about-list li strong {
  font-family: "poppins-bold", sans-serif;
  color: #313131;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 1.7rem;
  line-height: 3rem;
}

.about-content .about-list li span {
  display: block;
  font-family: "poppins-regular", sans-serif;
  color: #6e6e6e;
  font-size: 1.7rem;
  line-height: 1;
}

.about-content h3 {
  font-family: "poppins-bold", sans-serif;
  font-size: 1.7rem;
  line-height: 1.5;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  margin-bottom: 18px;
}

.about-content p {
  line-height: 1.5;
  margin-bottom: 18px;
}

.button-section {
  text-align: center;
  margin: 80px 0 80px 0;
}

.button-section .button {
  color: black;
  width: 190px;
  text-decoration: none;
  border: 3px solid black;
  padding: 1.2rem 3.2rem 1.2rem 3.2rem;
  font-size: 1.6rem;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  margin: 0 2rem 0 2rem;
}

.button-section .button:hover {
  border: 3px solid #0092cc;
  color: #0092cc;
}
